@import url('https://fonts.googleapis.com/css2?family=Lexend&display=swap');

/* === General Styling === */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Lexend', sans-serif;

}

/* === Full-page Centering === */
.body-container {
    display: grid;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    position: relative; /* Make it a positioned container */
     /* Hide parts of the image outside the container */
    background-color:#82C6C5;

}

/* === Sign-in/Sign-up Box === */
.signin-container {
    background-color: #58C0B6;
    width: 420px;
    max-height: 520px;
    border-radius: 30px;
    padding: 40px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.2);
    text-align: center;
    opacity: 0;
    transform: translateY(20px);
    animation: fadeInSlideUp 3s ease-out forwards;
    z-index: 2;
    position: relative;
}

/* === Form Styling === */
.form-container {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

/* === Input Fields === */
.input-field {
    width: 100%;
    padding: 12px;
    border: none;
    border-radius: 20px;
    font-size: 16px;
    background-color: #82C6C5;
    color: white;
    text-align: center;
    outline: none;
    transition: 0.3s;
}

.input-field::placeholder {
    color: white;
    opacity: 0.7;
}

.input-field:focus {
    background-color: #70B0AF;
}

/* === Button Styling === */
.signin-btn {
    padding: 15px;
    background-color: #51246B;
    color: white;
    width: 100%;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    margin-top: 20px;
    transition: 0.3s;
}

.signin-btn:hover {
    background-color: #9243ca;
}

.signin-btn:active {
    transform: scale(0.95);
}

/* === Link Styling === */
.link-text {
    margin-top: 15px;
    color: white;
    font-size: 14px;
}

.link {
    color: white;
    text-decoration: none;
    font-weight: bold;
    transition: 0.3s;
}

.link:hover {
    text-decoration: underline;
}

/* === Fade-in Animation === */
@keyframes fadeInSlideUp {
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* === Responsive Design === */
@media (max-width: 480px) {
    .signin-container {
        padding: 30px;
    }
}


/* === Error Message Styling === */
.error-text {
  color: #f44336; /* Red color for errors */
  font-size: 12px;
  margin-top: 4px; /* Adds spacing between input and error message */
}

/* === Logo Styling === */
.logo {
  display: block;
  margin: 0 auto 20px;  /* Centers the logo and adds some spacing below it */
  width: 120px;  /* Adjust the width to fit your design */
  height: auto;  /* Keep the aspect ratio */
}

.signin-container h2 {
    padding: 20px;
}

@keyframes cuteShake {
    0% { transform: translateX(0); }
    25% { transform: translateX(-5px); }
    50% { transform: translateX(5px); }
    75% { transform: translateX(-5px); }
    100% { transform: translateX(0); }
}

.bg-img {
    position: absolute;
    width: 200px; /* Adjust size */
    height: auto;
    opacity: 0.9; /* Make it slightly transparent */
    z-index: 1; /* Keeps it behind the sign-in box */
    animation: cuteShake 5.0s ease-in-out infinite;
}

/* Position individual images */
.bg-img-1 {
    bottom: -10%;
    left: 1%;
    height: auto;
    width:600px;

}


.bg-img-2 {
    bottom: -10%;
    right: 1%;
    height: auto;
    width:600px;

}
